<template>
  <div class="mb-12">
    <el-row type="flex" justify="space-between" class="label-row">
      <el-col :span="12">
        <span class="el-form-item__label">{{ name }}</span>
      </el-col>
      <el-col :span="12" class="text-right">
        <span>Count:</span>
        <span class="text-dark">{{ selectedValue ? selectedValue.count : '' }}</span>
      </el-col>
    </el-row>
    <el-select
      class="form-control"
      v-model="value"
      placeholder="Select Amount"
      @change="handleChange">
      <el-option
        v-for="option in options"
        :key="option.amount"
        :value="option.amount">
        <div class="option-grid">
          <div class="title">
            {{ option.amount }}
          </div>
          <div class="desc">
            {{ option.count }}
          </div>
        </div>
      </el-option>
    </el-select>
    <!--
    <el-slider
      v-model="sliderValue"
      :min="min"
      :max="max"
      @change="handleSliderChange"
    ></el-slider>
    -->
  </div>
</template>

<script>
export default {
  props: {
    name: String,
    defaultValue: {
      type: Object,
      default: null,
    },
    options: Array,
  },
  data() {
    return {
      value: '',
      selectedValue: null,
    };
  },
  computed: {
    fieldOptions() {
      const o =  this.options.map((v) => ({
        value: v,
        label: v,
      })) || [];
      console.log('o', o)
      return o
    },
  },
  methods: {
    handleChange(value) {
      this.value = value;
      const d = this.options.find((v) => v.amount == value)
      this.selectedValue = d;
      this.$emit('change',d);
    },

  },
  watch: {
    defaultValue(v) {
      this.selectedValue = v;
      this.value = v.amount
    }
  }
};
</script>

